import * as React from 'react'
import { useDeepCompareMemo } from 'use-deep-compare'
import { css } from 'emotion'
import { CSSObject } from '@emotion/core'
import styledSystemCss from '@styled-system/css'
import { compose } from 'styled-system'
import omit from 'lodash/omit'
import sortBy from 'lodash/sortBy'
import toPairs from 'lodash/toPairs'
import pick from 'lodash/pick'
import fromPairs from 'lodash/fromPairs'

import {
    borders,
    BorderProps,
    color,
    ColorProps,
    flex,
    FlexProps,
    layout,
    LayoutProps,
    position,
    PositionProps,
    sideBorders,
    SideBorderProps,
    space,
    SpaceProps,
} from '../../system/styles'
import { useTheme } from '../../utils'
// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore: .js file
import mergeCss from '../../utils/mergeCss'
import { Ref, CSSProp, HTMLAttributes } from '../../types'

export type SystemProps = BorderProps &
    SideBorderProps &
    FlexProps &
    LayoutProps &
    PositionProps &
    SpaceProps &
    ColorProps

type RenderProps = (
    props: {
        ref: Ref<HTMLElement>
    } & Omit<Props, 'as' | 'children' | 'omitProps' | 'wrapElement'>,
) => JSX.Element

export type Props = SystemProps & {
    children?: React.ReactNode | RenderProps
    css?: CSSProp
    as?: React.ElementType | keyof JSX.IntrinsicElements
    omitProps?: Array<string>
    wrapElement?: (element: React.ReactNode) => React.ReactElement
} & HTMLAttributes

const systemProps = [
    color.propNames,
    flex.propNames,
    layout.propNames,
    position.propNames,
    space.propNames,
    borders.propNames,
    sideBorders.propNames,
]
    .flat()
    .filter((value): value is string => !!value)

const prepareBorders = (calculatedBorders: Record<string, unknown>): Record<string, unknown> =>
    fromPairs(
        sortBy(toPairs(calculatedBorders), ([propertyName]) =>
            // ensure border-color is always BELOW border-*
            propertyName === 'borderColor' ? 1 : -1,
        ),
    )

const composedSystemBorderFns = compose(borders, sideBorders)
const composedSystemOtherFns = compose(color, flex, layout, position, space)

const focusStyles = {
    outline: 'none',
    // On firefox there is a inner focus ring created by that
    // `::-moz-focus-inner`. Since we have our own focus-style we remove it.
    '::-moz-focus-inner': {
        border: 0,
    },
}

const defaultStyles: CSSObject = {
    '&:focus:not(:focus-visible)': focusStyles,
    '&:focus:not(.focus-visible)': focusStyles,
    boxSizing: 'border-box',
    verticalAlign: 'top',
    fontFamily: 'inherit',
    margin: 0,
    padding: 0,
    borderWidth: 0,
}

export const Box = React.forwardRef<HTMLElement, Props>(
    (
        { as: asProp = 'div', children, css: cssProp, omitProps = [], className: classNameProp, wrapElement, ...props },
        ref,
    ) => {
        const theme = useTheme()

        const cssClassName = useDeepCompareMemo(
            () =>
                css([
                    defaultStyles,
                    prepareBorders(composedSystemBorderFns({ theme, ...props })),
                    composedSystemOtherFns({
                        theme,
                        ...props,
                    }),
                    styledSystemCss(mergeCss(cssProp))(theme),
                ]),
            [pick(props, systemProps), cssProp],
        )

        const className = `${cssClassName}${classNameProp ? ` ${classNameProp}` : ''}`

        if (typeof children === 'function') {
            return (children as RenderProps)({ className, ref, ...props })
        }

        const Component = asProp

        const element = (
            <Component
                className={className}
                ref={ref}
                {...(typeof asProp === 'string' ? omit(props, [...systemProps, ...omitProps]) : props)}
            >
                {children}
            </Component>
        )

        return wrapElement ? wrapElement(element) : element
    },
)

export default Box
