import { TLengthStyledSystem, system } from 'styled-system'
import * as CSS from 'csstype'

import { ResponsiveValue, Config } from '../styled-system-re-exported-types'

export type PositionProps = {
    zIndex?: ResponsiveValue<CSS.ZIndexProperty>
    top?: ResponsiveValue<CSS.TopProperty<TLengthStyledSystem>>
    right?: ResponsiveValue<CSS.RightProperty<TLengthStyledSystem>>
    bottom?: ResponsiveValue<CSS.BottomProperty<TLengthStyledSystem>>
    left?: ResponsiveValue<CSS.LeftProperty<TLengthStyledSystem>>
    position?: ResponsiveValue<CSS.PositionProperty>
}

export const positionConfig: Config<keyof PositionProps> = {
    position: true,
    zIndex: {
        property: 'zIndex',
        scale: 'zIndices',
    },
    top: {
        property: 'top',
        scale: 'space',
    },
    right: {
        property: 'right',
        scale: 'space',
    },
    bottom: {
        property: 'bottom',
        scale: 'space',
    },
    left: {
        property: 'left',
        scale: 'space',
    },
}

export const position = system(positionConfig)
