import { get, system } from 'styled-system'
import isNumber from 'lodash/isNumber'

import { SpaceAccessPaths, SpaceAccessPathsWithNegatives } from '../../theme/tokens/generated/tokenAccessPaths'
import { Config, ResponsiveValue } from '../styled-system-re-exported-types'

const marginTransform = (value: any, scale: any) => {
    if (value === 'auto') {
        return 'auto'
    }

    if (!isNumber(value)) {
        return get(scale, value, value)
    }

    const isNegative = value < 0
    const absolute = Math.abs(value)
    const resolvedValue = get(scale, absolute, absolute)

    return resolvedValue * (isNegative ? -1 : 1)
}

export type SpaceProps = {
    margin?: ResponsiveValue<SpaceAccessPathsWithNegatives | 'auto'>
    marginTop?: ResponsiveValue<SpaceAccessPathsWithNegatives | 'auto'>
    marginRight?: ResponsiveValue<SpaceAccessPathsWithNegatives | 'auto'>
    marginBottom?: ResponsiveValue<SpaceAccessPathsWithNegatives | 'auto'>
    marginLeft?: ResponsiveValue<SpaceAccessPathsWithNegatives | 'auto'>
    marginX?: ResponsiveValue<SpaceAccessPathsWithNegatives | 'auto'>
    marginY?: ResponsiveValue<SpaceAccessPathsWithNegatives | 'auto'>

    padding?: ResponsiveValue<SpaceAccessPaths>
    paddingTop?: ResponsiveValue<SpaceAccessPaths>
    paddingRight?: ResponsiveValue<SpaceAccessPaths>
    paddingBottom?: ResponsiveValue<SpaceAccessPaths>
    paddingLeft?: ResponsiveValue<SpaceAccessPaths>
    paddingX?: ResponsiveValue<SpaceAccessPaths>
    paddingY?: ResponsiveValue<SpaceAccessPaths>
}

export const spaceConfig: Config<keyof SpaceProps> = {
    margin: {
        property: 'margin',
        scale: 'space',
        transform: marginTransform,
    },
    marginTop: {
        property: 'marginTop',
        scale: 'space',
        transform: marginTransform,
    },
    marginRight: {
        property: 'marginRight',
        scale: 'space',
        transform: marginTransform,
    },
    marginBottom: {
        property: 'marginBottom',
        scale: 'space',
        transform: marginTransform,
    },
    marginLeft: {
        property: 'marginLeft',
        scale: 'space',
        transform: marginTransform,
    },
    marginX: {
        properties: ['marginLeft', 'marginRight'],
        scale: 'space',
        transform: marginTransform,
    },
    marginY: {
        properties: ['marginTop', 'marginBottom'],
        scale: 'space',
        transform: marginTransform,
    },
    padding: {
        property: 'padding',
        scale: 'space',
    },
    paddingTop: {
        property: 'paddingTop',
        scale: 'space',
    },
    paddingRight: {
        property: 'paddingRight',
        scale: 'space',
    },
    paddingBottom: {
        property: 'paddingBottom',
        scale: 'space',
    },
    paddingLeft: {
        property: 'paddingLeft',
        scale: 'space',
    },
    paddingX: {
        properties: ['paddingLeft', 'paddingRight'],
        scale: 'space',
    },
    paddingY: {
        properties: ['paddingTop', 'paddingBottom'],
        scale: 'space',
    },
}

export const space = system(spaceConfig)
