import * as PropTypes from 'prop-types'
import { isValidElementType } from 'react-is'

import * as constants from './constants'
import { isNil } from './isNil'

export const scalePropType = PropTypes.oneOf(Object.values(constants.scale))

export const spacePropType = PropTypes.oneOf(Object.values(constants.space))

export const sizePropType = PropTypes.oneOf(Object.values(constants.sizes))

export const variantPropType = PropTypes.oneOf(Object.values(constants.variants))

export const orientationPropType = PropTypes.oneOf(Object.values(constants.orientation))

export const systemPropType = PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string,
    PropTypes.array,
    PropTypes.object,
])

export const refPropType = PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({
        current: PropTypes.instanceOf('Element' in window ? window.Element : Object),
    }),
])

const CSSRuleShape = PropTypes.oneOfType([PropTypes.func, PropTypes.object])

export const cssPropType = PropTypes.oneOfType([CSSRuleShape, PropTypes.array])

export const within100 = (props, propName, componentName) => {
    if (props[propName]) {
        const value = props[propName]

        if (typeof value === 'number' || typeof value === 'string') {
            return value >= 0 && value <= 100
                ? null
                : new Error(`${propName} in ${componentName} is not within 0 and 100`)
        }
    }

    return null
}

export const onlyTwoChildren = (props, propName, componentName) => {
    if (props.children && props.children.length !== 2) {
        return new Error(
            `Invalid prop ${propName} supplied to ${componentName}. ${componentName} should have exactly two children.`,
        )
    }

    if (propName !== 'children') {
        return new Error(`Invalid prop function supplied to ${componentName}. Use onlyTwoChildren only for children.`)
    }

    return null
}

function getComponentValidator() {
    function checkType(isRequired, props, propName, componentName, location, propFullName) {
        const value = props[propName]

        if (isNil(value)) {
            if (isRequired) {
                if (value === null) {
                    return new Error(
                        `The ${location} '${propFullName}' is marked as required in '${componentName}', but its value is 'null'.`,
                    )
                }

                return new Error(
                    `The ${location} '${propFullName}' is marked as required in '${componentName}', but its value is 'undefined'.`,
                )
            }
        } else if (!isValidElementType(value)) {
            return new Error(
                `Invalid ${location} '${propFullName}' supplied to ${componentName}. Expected a string (for built-in components) or a class/function (for composite components).`,
            )
        }

        return null
    }

    const mutableChainedCheckType = checkType.bind(null, false)

    mutableChainedCheckType.isRequired = checkType.bind(null, true)

    return mutableChainedCheckType
}

export const ValidElementType = {
    component: getComponentValidator(),
}

export const disclosurePropType = {
    as: PropTypes.elementType,
    disabled: PropTypes.bool,
    focusable: PropTypes.bool,
    visible: PropTypes.bool,
    toggle: PropTypes.func,
}
