import * as React from 'react'
import * as PropTypes from 'prop-types'
import { omit } from 'lodash'
import { useButton } from 'reakit/Button'

import { cssPropType, sizePropType, variantPropType } from '../../utils/sharedPropTypes'
import { useTheme } from '../../utils'
import { useFocusRing, useFocusStyle } from '../../utils/styleHooks'
import { BorderBox } from '../BorderBox'
import { themeGet } from '../../system'
import { CircularProgress } from '../CircularProgress'
// eslint-disable-next-line import/no-cycle
import { GroupContext } from '../Group'
import * as constants from '../../utils/constants'
import * as sizeMapping from '../../utils/sizeMapping'
import useReakitHook from '../../utils/useReakitHook'
import { SlotProvider } from '../../utils/components/Slots'

const { XSMALL, SMALL, MEDIUM, LARGE, XLARGE } = constants.sizes

const isCircularProgress = (element) => element?.type === CircularProgress

const isIcon = (element) => element?.type?.displayName?.startsWith('Svg')

const sizeIconPaddingMap = {
    [XSMALL]: '1',
    [SMALL]: '1',
    [MEDIUM]: '2',
    [LARGE]: '2',
    [XLARGE]: '2',
}

export const ButtonBase = React.forwardRef(
    ({ children, size, variant = 'brand', style, block, css = {}, ...props }, ref) => {
        const theme = useTheme()

        const { size: groupSize, ...groupProps } = React.useContext(GroupContext)

        const actualSize = size || groupSize || constants.BUTTON_SIZE_DEFAULT

        const defaultPadding = sizeMapping.padding[actualSize]
        const iconPadding = sizeMapping.icon[actualSize]
        const mutablePaddings = {
            paddingLeft: defaultPadding,
            paddingRight: defaultPadding,
        }

        const isOnlyChild = React.Children.toArray(children).length === 1

        const childElements = React.Children.map(children, (child, index) => {
            const isCircularProgressElement = isCircularProgress(child)
            const isIconElement = isIcon(child)
            const isIconLikeElement = isIconElement || isCircularProgressElement

            if (isOnlyChild) {
                if (!isIconLikeElement) {
                    return child
                }

                mutablePaddings.paddingLeft = iconPadding
                mutablePaddings.paddingRight = iconPadding

                return React.cloneElement(child, {
                    paddingLeft: 0,
                    paddingRight: 0,
                })
            }

            if (isIconLikeElement) {
                mutablePaddings[index === 0 ? 'paddingLeft' : 'paddingRight'] = iconPadding

                return React.cloneElement(child, {
                    [index === 0 ? 'paddingRight' : 'paddingLeft']: sizeIconPaddingMap[actualSize],
                })
            }

            return child
        })
        const behaviours = {
            disabled: {
                color: 'gray.3',
                cursor: 'not-allowed',
            },
            focus: {
                textDecoration: 'none',
            },
            hover: {
                textDecoration: 'none',
            },
        }

        const fontSize = themeGet(`fontSizes.${sizeMapping.fontSize[actualSize]}`)({
            theme,
        })

        return (
            <SlotProvider
                slots={{
                    circularProgress: {
                        backgroundColor: 'transparent',
                        color: props.disabled ? behaviours.disabled.color : props.color,
                        size: sizeMapping.wrappedIconSizeMap[actualSize],
                    },
                    icon: {
                        size: sizeMapping.wrappedIconSizeMap[actualSize],
                    },
                }}
            >
                <BorderBox
                    as="button"
                    display="inline-flex"
                    borderRadius={1}
                    width={block ? '100%' : 'auto'}
                    borderColor="transparent"
                    height={sizeMapping.height[actualSize]}
                    backgroundColor="transparent"
                    css={[
                        {
                            fontSize: sizeMapping.fontSize[actualSize],
                            lineHeight: `${fontSize}px`,
                            alignItems: 'center',
                            justifyContent: 'center',
                            whiteSpace: 'nowrap',
                            cursor: 'pointer',
                            textDecoration: 'none',
                            '&:hover': behaviours.hover,
                            '&:focus': behaviours.focus,
                            ...useFocusRing(useFocusStyle(variant)),
                            '&[aria-disabled="true"]': behaviours.disabled,
                        },
                        css,
                    ]}
                    {...mutablePaddings}
                    {...omit(
                        useReakitHook(useButton, {
                            ref,
                            ...groupProps,
                            style,
                            ...props,
                        }),
                        'style.pointerEvents',
                    )}
                >
                    {childElements}
                </BorderBox>
            </SlotProvider>
        )
    },
)

ButtonBase.displayName = 'ButtonBase'

ButtonBase.propTypes = {
    size: sizePropType,
    variant: variantPropType,
    children: PropTypes.node,
    as: PropTypes.elementType,
    css: cssPropType,
    block: PropTypes.bool,
    disabled: PropTypes.bool,
    focusable: PropTypes.bool,
    color: PropTypes.string,
    style: PropTypes.object,
}
