import * as React from 'react'
import * as PropTypes from 'prop-types'
import { keyframes } from 'emotion'

import { themeGet } from '../../system'
import { useTheme } from '../../utils/useTheme'
import { cssPropType, systemPropType, variantPropType, within100 } from '../../utils/sharedPropTypes'
import * as constants from '../../utils/constants'
import { useSlotProps } from '../../utils/components/Slots'
import Box from '../Box'

const indeterminate = {
    '0%': { transform: 'rotateZ(0)' },
    '100%': { transform: 'rotate(360deg)' },
}

const { XSMALL, SMALL, MEDIUM, LARGE, XLARGE } = constants.sizes

const iconSizeMap = {
    [XSMALL]: 3,
    [SMALL]: 4,
    [MEDIUM]: 5,
    [LARGE]: 6,
    [XLARGE]: 7,
}

const strokeWidthMapping = {
    [XSMALL]: '3',
    [SMALL]: '2',
    [MEDIUM]: '2',
    [LARGE]: '2',
    [XLARGE]: '2',
}

/**
 * > Inherits from `<Icon />`
 */

export const CircularProgress = React.forwardRef(
    // eslint-disable-next-line react/prop-types
    ({ variant = 'brand', value, css, ...props }, ref) => {
        const { backgroundColor, size, ...slotProps } = useSlotProps(props, 'circularProgress')
        const theme = useTheme()
        const isIndeterminate = value === undefined || value === null

        const actualValue = value || 25
        const strokeWidth = themeGet(
            `space.${strokeWidthMapping[size]}`,
            parseInt(size),
        )({
            theme,
        })

        const radius = 100 / 2
        const actualRadius = radius - strokeWidth / 2
        const circumference = actualRadius * 2 * Math.PI
        const offset = circumference - (actualValue / 100) * circumference + strokeWidth / 2

        const keyFrameClassIndeterminate = keyframes(indeterminate)

        return (
            <Box
                as="svg"
                ref={ref}
                viewBox="0 0 100 100"
                fill="transparent"
                color={`${variant}.5`}
                padding="2px"
                size={iconSizeMap[size] || size}
                css={[
                    css,
                    {
                        boxSizing: 'border-box',
                        '& > *': {
                            content: '""',
                            ...(isIndeterminate
                                ? {
                                      animation: `${keyFrameClassIndeterminate} ${theme.duration.slowest} ${theme.timingFunction.linear} infinite`,
                                  }
                                : {
                                      transition: `stroke-dashoffset ${theme.duration.slow} ${theme.timingFunction.linear} 0s`,
                                  }),
                            transform: 'rotate(-90deg)',
                            transformOrigin: '50% 50%',
                        },
                    },
                ]}
                {...slotProps}
            >
                <circle
                    r={actualRadius}
                    cx={radius}
                    cy={radius}
                    strokeWidth={strokeWidth}
                    stroke={themeGet(`colors.${backgroundColor || 'gray.2'}`)({
                        theme,
                    })}
                />
                <circle
                    r={actualRadius}
                    cx={radius}
                    cy={radius}
                    strokeLinecap="round"
                    strokeWidth={strokeWidth}
                    stroke="currentcolor"
                    strokeDasharray={`${circumference} ${circumference}`}
                    strokeDashoffset={offset}
                />
            </Box>
        )
    },
)

CircularProgress.propTypes = {
    variant: variantPropType,
    size: systemPropType,
    css: cssPropType,
    backgroundColor: PropTypes.string,
    value: within100,
}

CircularProgress.displayName = 'CircularProgress'

CircularProgress.defaultProps = {
    size: constants.sizes.SMALL,
}

CircularProgress.displayName = 'CircularProgress'
