import * as React from 'react'

import { Box } from '../Box'

type Props = {
    onFocus: (event: React.FocusEvent<HTMLDivElement>) => void
}

export const FocusGuard = (props: Props) => (
    <Box
        css={{
            position: 'fixed',
            width: '1px',
            height: 0,
            overflow: 'hidden',
            padding: 0,
            top: 0,
            left: 0,
        }}
        tabIndex={0}
        {...props}
    />
)
