import { grid } from './Grid'

const numberToPx = (value: number) => `${value}px`

const tokens = [
    numberToPx(grid * 2),
    numberToPx(grid * 2),
    numberToPx(grid * 3),
    numberToPx(grid * 4),
    numberToPx(grid * 4),
    numberToPx(grid * 6),
    numberToPx(grid * 7),
    numberToPx(grid * 8),
    numberToPx(grid * 10),
] as const

const aliases = {
    'xxx-small': tokens[0],
    'xx-small': tokens[1],
    'x-small': tokens[2],
    small: tokens[3],
    medium: tokens[4],
    large: tokens[5],
    'x-large': tokens[6],
    'xx-large': tokens[7],
    'xxx-large': tokens[8],
} as const

// eslint-disable-next-line functional/immutable-data
export const lineHeights = Object.assign(tokens, aliases)
