import { borders } from './tokens/Borders'
import { sideBorders } from './tokens/SideBorders'
import { breakpoints } from './tokens/Breakpoints'
import { colors, Colors } from './tokens/Colors'
import { fonts } from './tokens/Fonts'
import { fontSizes } from './tokens/FontSizes'
import { fontWeights } from './tokens/FontWeights'
import { grid } from './tokens/Grid'
import { letterSpacings } from './tokens/LetterSpacings'
import { lineHeights } from './tokens/LineHeights'
import { mediaQueries } from './tokens/MediaQueries'
import { radii } from './tokens/Radii'
import { shadows } from './tokens/Shadows'
import { space } from './tokens/Space'
import { timingFunction } from './tokens/TimingFunction'
import { zIndices } from './tokens/ZIndices'
import { Theme } from './types'
import { duration } from './tokens/Duration'

export const createTheme = (overrides?: { colors: Partial<Colors> }): Theme => {
    const colorsWithOverrides: Colors = { ...colors, ...overrides?.colors }

    const calculatedColors = Object.fromEntries(
        Object.entries(colorsWithOverrides).map(([key, value]) => [
            key,
            typeof value === 'function' ? value(colorsWithOverrides) : value,
        ]),
    ) as Colors

    return {
        breakpoints,
        mediaQueries,
        fonts,
        fontSizes,
        fontWeights,
        lineHeights,
        letterSpacings,
        space,
        grid,
        zIndices,
        shadows,
        borders,
        sideBorders,
        radii,
        colors: calculatedColors,
        timingFunction,
        duration,
    }
}
