import { LayoutProps } from '../system/styles/layout'
import { sizes } from './constants'

type Size = LayoutProps['size']

type SizeMap = { [key: string]: Size }

export const { XSMALL, SMALL, MEDIUM, LARGE, XLARGE } = sizes

export const height: SizeMap = {
    [XSMALL]: '3',
    [SMALL]: '4',
    [MEDIUM]: '5',
    [LARGE]: '6',
    [XLARGE]: '7',
}

export const padding: SizeMap = {
    [XSMALL]: '2',
    [SMALL]: '2',
    [MEDIUM]: '3',
    [LARGE]: '3',
    [XLARGE]: '4',
}

// TODO move/remove scope
export const fontSize: SizeMap = {
    [XSMALL]: '0',
    [SMALL]: '1',
    [MEDIUM]: '2',
    [LARGE]: '3',
    [XLARGE]: '3',
}

export const icon: SizeMap = {
    [XSMALL]: '1',
    [SMALL]: '1',
    [MEDIUM]: '1',
    [LARGE]: '2',
    [XLARGE]: '2',
}

// When the `CircularProgress` component is wrapped by another component,
// you can use this size map because it results one size smaller icons than
// the size of the wrapper component.
export const wrappedCircularProgressSizeMap = {
    [XSMALL]: XSMALL,
    [SMALL]: XSMALL,
    [MEDIUM]: SMALL,
    [LARGE]: MEDIUM,
    [XLARGE]: MEDIUM,
}

// When the `Icon` component is wrapped by another component, you can use this
// size map because it results one size smaller icons than the size of the
// wrapper component.
export const wrappedIconSizeMap = {
    [XSMALL]: '14px',
    [SMALL]: XSMALL,
    [MEDIUM]: SMALL,
    [LARGE]: MEDIUM,
    [XLARGE]: LARGE,
}
