const tokens = [
    'none',
    '0 2px 5px 0 rgba(0,0,0,.17), 0 0 10px 0 rgba(0,0,0,.08)',
    '0 6px 13px 0 rgba(0,0,0,.15), 0 0 10px 0 rgba(0,0,0,.08)',
    '0 15px 20px 0 rgba(0,0,0,.14), 0 0 10px 0 rgba(0,0,0,.08)',
] as const

const aliases = {
    zero: tokens[0],
    small: tokens[1],
    medium: tokens[2],
    large: tokens[3],
} as const

// eslint-disable-next-line functional/immutable-data
export const shadows = Object.assign(tokens, aliases)
