import * as React from 'react'
import { CSSObject } from '@emotion/core'

import { Box } from '../Box'

type Props = {
    style: React.CSSProperties
    children: React.ReactNode
    css?: CSSObject
    onKeyDown: (event: React.KeyboardEvent) => void
}

export const PopoverBox = React.forwardRef<HTMLDivElement, Props>(({ children, style, css, ...props }, ref) => (
    <Box
        backgroundColor="white"
        color="black"
        borderRadius={1}
        boxShadow={2}
        ref={ref}
        css={[
            {
                ...(style as any), // FIXME: ds#2453
                zIndex: 8,
                outline: 0,
            },
            css,
        ]}
        tabIndex={-1}
        role="dialog"
        {...props}
    >
        {children}
    </Box>
))
