const tokens = [10, 12, 16, 20, 24, 32, 40, 48, 64] as const

const aliases = {
    'xxx-small': tokens[0],
    'xx-small': tokens[1],
    'x-small': tokens[2],
    small: tokens[3],
    medium: tokens[4],
    large: tokens[5],
    'x-large': tokens[6],
    'xx-large': tokens[7],
    'xxx-large': tokens[8],
} as const

// eslint-disable-next-line functional/immutable-data
export const fontSizes = Object.assign(tokens, aliases)
