const tokens = ['linear', 'ease-in', 'ease-out', 'cubic-bezier(0.45,0.05,0.55,0.95)'] as const

const aliases = {
    linear: tokens[0],
    'ease-in': tokens[1],
    'ease-out': tokens[2],
    'ease-in-out': tokens[3],
} as const

// eslint-disable-next-line functional/immutable-data
export const timingFunction = Object.assign(tokens, aliases)
