import * as React from 'react'

import { useFocusRing, useFocusStyle } from '../../utils/styleHooks'
import { ButtonBase } from './ButtonBase'

export const Button = React.forwardRef(({ variant = 'brand', css, ...props }, ref) => {
    const behaviours = {
        active: {
            ':not([aria-disabled="true"])': {
                backgroundColor: `${variant}.7`,
                color: 'white',
            },
        },
        disabled: {
            backgroundColor: 'gray.1',
            borderColor: 'gray.3',
            color: 'gray.3',
        },
        focus: {
            ':not([aria-disabled="true"])': {
                backgroundColor: `${variant}.5`,
            },
            color: 'white',
        },
        hover: {
            ':not([aria-disabled="true"])': {
                backgroundColor: `${variant}.6`,
                color: 'white',
            },
        },
    }

    return (
        <ButtonBase
            ref={ref}
            backgroundColor={`${variant}.5`}
            borderColor="transparent"
            color="white"
            css={[
                {
                    '&:hover': behaviours.hover,
                    '&:focus': behaviours.focus,
                    ...useFocusRing(useFocusStyle(variant)),
                    '&:active': behaviours.active,
                    '&[aria-disabled="true"]': behaviours.disabled,
                },
                css,
            ]}
            {...props}
        />
    )
})

Button.displayName = 'Button'

Button.propTypes = ButtonBase.propTypes

Button.defaultProps = ButtonBase.defaultProps
