import { get, system, TLengthStyledSystem } from 'styled-system'
import * as CSS from 'csstype'
import isNumber from 'lodash/isNumber'

import { ResponsiveValue, Config } from '../styled-system-re-exported-types'
import { SpaceAccessPaths } from '../../theme/tokens/generated/tokenAccessPaths'

const widthTransform = (value: any, scale: any) =>
    get(scale, value, !isNumber(value) || value > 1 ? value : `${value * 100}%`)

export type LayoutProps = {
    width?: ResponsiveValue<CSS.WidthProperty<TLengthStyledSystem>>
    height?: ResponsiveValue<CSS.HeightProperty<TLengthStyledSystem>>
    minWidth?: ResponsiveValue<CSS.MinWidthProperty<TLengthStyledSystem>>
    minHeight?: ResponsiveValue<CSS.MinHeightProperty<TLengthStyledSystem>>
    maxWidth?: ResponsiveValue<CSS.MaxWidthProperty<TLengthStyledSystem>>
    maxHeight?: ResponsiveValue<CSS.MaxHeightProperty<TLengthStyledSystem>>
    size?: ResponsiveValue<SpaceAccessPaths>
    overflow?: ResponsiveValue<CSS.OverflowProperty>
    display?: ResponsiveValue<CSS.DisplayProperty>
    verticalAlign?: ResponsiveValue<CSS.VerticalAlignProperty<TLengthStyledSystem>>
    flex?: ResponsiveValue<CSS.FlexProperty<TLengthStyledSystem>>
    flexGrow?: ResponsiveValue<CSS.GlobalsNumber>
    flexShrink?: ResponsiveValue<CSS.GlobalsNumber>
    flexBasis?: ResponsiveValue<CSS.FlexBasisProperty<TLengthStyledSystem>>
    justifySelf?: ResponsiveValue<CSS.JustifySelfProperty>
    alignSelf?: ResponsiveValue<CSS.AlignSelfProperty>
    order?: ResponsiveValue<CSS.GlobalsNumber>

    gridGap?: ResponsiveValue<CSS.GridGapProperty<TLengthStyledSystem>>
    gridRowGap?: ResponsiveValue<CSS.GridRowGapProperty<TLengthStyledSystem>>
    gridColumnGap?: ResponsiveValue<SpaceAccessPaths>
    gridColumn?: ResponsiveValue<CSS.GridColumnProperty>
    gridRow?: ResponsiveValue<CSS.GridRowProperty>
    gridArea?: ResponsiveValue<CSS.GridAreaProperty>
    gridAutoFlow?: ResponsiveValue<CSS.GridAutoFlowProperty>
    gridAutoRows?: ResponsiveValue<CSS.GridAutoRowsProperty<TLengthStyledSystem>>
    gridAutoColumns?: ResponsiveValue<CSS.GridAutoColumnsProperty<TLengthStyledSystem>>
    gridTemplateRows?: ResponsiveValue<CSS.GridTemplateRowsProperty<TLengthStyledSystem>>
    gridTemplateColumns?: ResponsiveValue<CSS.GridTemplateColumnsProperty<TLengthStyledSystem>>
    gridTemplateAreas?: ResponsiveValue<CSS.GridTemplateAreasProperty>
}

export const layoutConfig: Config<keyof LayoutProps> = {
    width: {
        property: 'width',
        scale: 'space',
        transform: widthTransform,
    },
    height: {
        property: 'height',
        scale: 'space',
    },
    minWidth: {
        property: 'minWidth',
        scale: 'space',
    },
    minHeight: {
        property: 'minHeight',
        scale: 'space',
    },
    maxWidth: {
        property: 'maxWidth',
        scale: 'space',
    },
    maxHeight: {
        property: 'maxHeight',
        scale: 'space',
    },
    size: {
        properties: ['width', 'height'],
        scale: 'space',
    },
    overflow: true,
    display: true,
    verticalAlign: true,

    // flexitem
    flex: true,
    flexGrow: true,
    flexShrink: true,
    flexBasis: true,
    justifySelf: true,
    alignSelf: true,
    order: true,

    gridGap: {
        property: 'gridGap',
        scale: 'space',
    },
    gridRowGap: {
        property: 'gridRowGap',
        scale: 'space',
    },
    gridColumnGap: {
        property: 'gridColumnGap',
        scale: 'space',
    },
    gridColumn: true,
    gridRow: true,
    gridArea: true,
    gridAutoFlow: true,
    gridAutoRows: true,
    gridAutoColumns: true,
    gridTemplateRows: true,
    gridTemplateColumns: true,
    gridTemplateAreas: true,
}

export const layout = system(layoutConfig)
