import * as CSS from 'csstype'
import { system } from 'styled-system'

import { ColorsAccessPaths } from '../../theme/tokens/generated/tokenAccessPaths'
import { ResponsiveValue, Config } from '../styled-system-re-exported-types'

export type ColorProps = {
    color?: ResponsiveValue<ColorsAccessPaths | CSS.Globals>
    backgroundColor?: ResponsiveValue<ColorsAccessPaths | CSS.Globals>
    opacity?: ResponsiveValue<CSS.GlobalsNumber>
}

export const colorConfig: Config<keyof ColorProps> = {
    color: {
        property: 'color',
        scale: 'colors',
    },
    backgroundColor: {
        property: 'backgroundColor',
        scale: 'colors',
    },
    opacity: true,
}

export const color = system(colorConfig)
