import { grid } from './Grid'

const tokens = [grid * 0, grid * 0.5, grid * 1, grid * 2, grid * 3, grid * 4, grid * 5, grid * 6] as const

const aliases = {
    zero: tokens[0],
    'xx-small': tokens[1],
    'x-small': tokens[2],
    small: tokens[3],
    medium: tokens[4],
    large: tokens[5],
    'x-large': tokens[6],
    'xx-large': tokens[7],
} as const

// eslint-disable-next-line functional/immutable-data
export const space = Object.assign(tokens, aliases)
