import * as React from 'react'
import { Popper } from 'react-popper'
import { Modifier, Placement } from '@popperjs/core'
import { CSSObject } from '@emotion/core'

import { useTheme } from '../../utils'
import { useOutsideClick } from '../../utils/useOutsideClick'
import { useControlled } from '../../utils/useControlled'
import { callAll } from '../../utils/callAll'
import { Portal } from '../Portal'
import { FocusGuard } from './FocusGuard'
import { PopoverBox } from './PopoverBox'
import { useComposeRefs } from '../../utils/composeRefs'

type RenderProps = (props: { hide: () => void }) => React.ReactNode

type Props = {
    /** When children is a function it receives a hide function that closes the Popover   */
    children: RenderProps | React.ReactNode
    trigger: any // FIXME: ds#2453
    placement?: Placement
    visible?: boolean
    onOpen?: () => void
    onClose?: () => void
    onAfterOpen?: () => void
    disablePortal?: boolean
    disableAutoFocus?: boolean
    css?: CSSObject
}

export const Popover: React.FC<Props> = ({
    children,
    placement,
    visible: visibleProp,
    onOpen,
    onClose,
    trigger,
    onAfterOpen,
    disablePortal,
    disableAutoFocus,
    ...props
}) => {
    const [visible, setUncontrolledVisible] = useControlled(visibleProp, false, 'Popover')

    const [popoverBoxKey, setPopoverBoxKey] = React.useState<0 | 1>(0)

    const referenceRef = React.useRef<HTMLElement>(null)
    const popperRef = React.useRef<HTMLDivElement>(null)
    const onAfterOpenFiredRef = React.useRef<boolean>(false)

    const theme = useTheme()

    const show = React.useCallback(() => {
        if (onOpen) {
            onOpen()
        }

        setUncontrolledVisible(true)
    }, [onOpen, setUncontrolledVisible])

    const hide = React.useCallback(() => {
        if (onClose) {
            onClose()
        }

        setPopoverBoxKey(0)
        setUncontrolledVisible(false)
    }, [onClose, setUncontrolledVisible])

    /*
     * Force update when component mounts and
     * visible prop is set to true. This is required
     * because on the very first render the reference
     * element is not attached to the trigger.
     */
    const forceUpdate = React.useReducer(() => ({}), {})[1]

    React.useEffect(() => {
        if (visible) {
            forceUpdate()
        }
    }, []) // eslint-disable-line react-hooks/exhaustive-deps

    const handleClickOutside = React.useCallback(
        (event) => {
            /*
             * Do not fire the `hide` function when outside click
             * take place on a popover trigger or any of its
             * children to prevent opening it again right away
             * caused by trigger's onClick callback.
             */
            if (!referenceRef.current?.contains(event.target)) {
                hide()
            }
        },
        [hide],
    )

    useOutsideClick(popperRef, handleClickOutside)

    /*
     * Handle "Escape" key press.
     *
     * does not get triggered on esc when an ancestor is focused
     * TODO: https://github.com/pmedianetwork/design-system/issues/1531
     */
    const handleKeyDown = React.useCallback(
        (event: React.KeyboardEvent) => {
            if (event.key !== 'Escape') {
                return
            }
            event.stopPropagation()
            hide()
        },
        [hide],
    )

    /*
     * Bring the focus back to the last
     * activeElement after close.
     */
    const lastActiveElement = React.useRef<HTMLElement | null>(null)

    React.useEffect(() => {
        if (!disableAutoFocus) {
            if (visible) {
                lastActiveElement.current = document.activeElement as HTMLElement
            } else if (lastActiveElement.current) {
                lastActiveElement.current.focus({ preventScroll: true })
                lastActiveElement.current = null
            }
        }

        if (!visible) {
            onAfterOpenFiredRef.current = false
        }
    }, [visible, disableAutoFocus])

    const popperModifiers: Array<Partial<Modifier<any>>> = [
        {
            name: 'offset',
            options: {
                offset: [0, theme.space['x-small']],
            },
        },
        {
            name: 'arrow',
            enabled: false,
        },
        /*
         * onAfterOpen callback fires only once
         * after the Popover is fully positioned.
         */
        {
            name: 'onAfterOpen',
            enabled: true,
            phase: 'afterWrite',
            fn: () => {
                if (!onAfterOpenFiredRef.current) {
                    /*
                     * Change the key of the PopoverBox element so React will
                     * treat it as a new element causing re-render of the whole
                     * content, thus allowing for `autoFocus` attr to work.
                     */
                    setPopoverBoxKey(1)

                    /*
                     * Focus the Popover container only if
                     * it does not contain any focused element,
                     * e.g. by autoFocus attr or onAfterOpen callback.
                     */
                    if (popperRef.current && !popperRef.current.contains(document.activeElement) && !disableAutoFocus) {
                        popperRef.current.focus()
                    }

                    if (onAfterOpen) {
                        onAfterOpen()
                    }
                    onAfterOpenFiredRef.current = true
                }
            },
        },
    ]

    const MaybePortal = disablePortal ? React.Fragment : Portal
    const onClick = React.useMemo(() => callAll(visible ? hide : show, trigger?.props?.onClick), [
        visible,
        hide,
        show,
        trigger,
    ])

    const triggerRef = useComposeRefs(referenceRef, trigger?.ref)

    return (
        <>
            {trigger &&
                React.cloneElement(trigger, {
                    ref: triggerRef,
                    onClick,
                })}

            {visible && referenceRef.current ? (
                <Popper
                    referenceElement={referenceRef.current}
                    placement={placement}
                    innerRef={popperRef}
                    modifiers={popperModifiers}
                >
                    {({ ref, style }) => {
                        const childrenCalculated =
                            typeof children === 'function'
                                ? /*
                                   * Cast to RenderProps to prevent typescript error that
                                   * prompts due to a broken typeof guard.
                                   * https://github.com/microsoft/TypeScript/issues/37663
                                   */
                                  (children as RenderProps)({
                                      hide,
                                  })
                                : children

                        return (
                            <MaybePortal>
                                <FocusGuard onFocus={hide} />
                                <PopoverBox
                                    ref={ref}
                                    style={style}
                                    key={popoverBoxKey}
                                    onKeyDown={handleKeyDown}
                                    {...props}
                                >
                                    {childrenCalculated}
                                </PopoverBox>
                                <FocusGuard onFocus={hide} />
                            </MaybePortal>
                        )
                    }}
                </Popper>
            ) : null}
        </>
    )
}

Popover.displayName = 'Popover'

export default Popover
