import Color from 'color'

export const lighten = (ratio, color) => Color(color).lighten(ratio)

export const setAlpha = (alpha, color) => Color(color).alpha(alpha)

export const readableColor = (color) => (Color(color).isDark() ? '#fff' : '#000')

export const isValidColor = (color) => {
    try {
        Color(color.toString())
    } catch (exception) {
        return false
    }

    return true
}
