// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore: .js file
import { setAlpha } from '../../utils/color'

type Color = string | ((colors: Colors) => string)

type ColorPalette = [string, string, string, string, string, string, string, string, string, string]

export type Colors = {
    gray: ColorPalette
    blue: ColorPalette
    green: ColorPalette
    red: ColorPalette
    yellow: ColorPalette
    orange: ColorPalette
    cyan: ColorPalette
    purple: ColorPalette
    brand: ColorPalette
    success: ColorPalette
    warning: ColorPalette
    danger: ColorPalette
    black: string
    white: string
    shadow: [string]

    // Text
    text: Color
    textSecondary: Color
    // Background
    background: Color
    // Primary
    primary: Color
    primaryHighlighted: Color
    primaryActive: Color
    // Secondary
    secondary: Color
    secondaryHighlighted: Color
    secondaryActive: Color
    // Muted (i.e. disabled)
    muted: Color
    mutedSecondary: Color
    // Accent (i.e. Link)
    accent: Color
    accentHighlighted: Color

    // CSS type safe
    transparent: 'transparent'
    current: 'currentColor'
}

