// functional palettes
const brand = blue
const success = green
const warning = yellow
const danger = red

const black = '#000'
const white = '#fff'

const shadow: Colors['shadow'] = [setAlpha(0.25, gray[7]).toString()]

export const colors: Colors = {
    black,
    white,
    gray,
    blue,
    green,
    red,
    yellow,
    orange,
    cyan,
    purple,
    shadow,
    brand,
    success,
    warning,
    danger,
    // Text
    text: (color: Colors) => color.gray[9],
    textSecondary: (color: Colors) => color.gray[7],
    // Background
    background: (color: Colors) => color.white,
    // Primary
    primary: (color: Colors) => color.brand[5],
    primaryHighlighted: (color: Colors) => color.brand[6],
    primaryActive: (color: Colors) => color.brand[7],
    // Secondary
    secondary: (color: Colors) => color.brand[0],
    secondaryHighlighted: (color: Colors) => color.brand[1],
    secondaryActive: (color: Colors) => color.brand[2],
    // Muted (i.e. disabled)
    muted: (color: Colors) => color.gray[3],
    mutedSecondary: (color: Colors) => color.gray[1],
    // Accent (i.e. Link)
    accent: (color: Colors) => color.blue[8],
    accentHighlighted: (color: Colors) => color.blue[7],

    // CSS type safe
    transparent: 'transparent' as const,
    current: 'currentColor' as const,
}
