import 'focus-visible'
import { themeGet } from '../system'
import { useTheme } from './useTheme'
import { setAlpha } from './color'

/**
 * Hooks to generate consistent focusStyle properties for focus rings
 *
 * The styles are separated from the focus selector because not all components
 * use the same css for focusStyle. This is the most common one.
 */

export const useFocusStyle = (color = 'brand', minimal = false) => {
    const theme = useTheme()

    return {
        outline: 'none',
        ...(!minimal && {
            borderColor: `${color}.9`,
        }),
        boxShadow: `0 0 0 4px ${setAlpha(
            0.3,
            themeGet(`colors.${color}.5`)({
                theme,
            }),
        )}`,
    }
}

export const useFocusRing = (styles) => ({
    '&.focus-visible': styles,
    '&:focus-visible': styles,
    '&[data-focus-visible-added]': styles,
})
